import React from "react";

export default function Dialogue(props) {
  return (
    <div className="dialogBase">
      <div className="characterName">{props.charname}</div>
      <div
        className="dialogText"
        // Advance the text whenever the dialog box is clicked on
        onClick={() => {
          props.advance();
        }}
      >
        {props.diatext}
      </div>
    </div>
  );
}
