import React, { useState, useEffect } from "react";

import CharacterDisplay from "./CharacterDisplay";
import Dialogue from "./Dialogue";
import Buttons from "./Buttons";

//To do:
// - Add more story
// - See if I can add a typewriter effect

export default function Vnovel() {
  const [pos, setPos] = useState(0);
  const [dialog, setDialog] = useState({});
  //Add the super-important loading boolean; see below
  const [loading, setLoading] = useState(true);

  //On page load, get the dialogue
  useEffect(() => {
    fetch("data/dialog.json")
      .then((result) => result.json())
      .then((data) => {
        setDialog(data);
        setLoading(false);
      });
  }, []);

  //Wait for the dialog data to load, then start showing the components
  //Again, thanks to Four from StackOverflow
  if (loading === false) {
    return (
      <div className="App">
        <h1>Bill and Carol</h1>
        <CharacterDisplay portrait={dialog[pos].portrait} bg={dialog[pos].bg} />
        <Dialogue
          diatext={dialog[pos].dialog}
          charname={dialog[pos].name}
          advance={advance}
        />
        <Buttons back={back} savepos={savepos} loadpos={loadpos} />
        <h3>
          Hey, guess what? For some reason, I didn't properly read through the
          project requirements and missed key info. I might have to start over,
          with less time than I started.
        </h3>
      </div>
    );
  }

  // Advance by one dialog
  function advance() {
    console.log(dialog.length);
    //To avoid advancing past the last dialog
    if (pos < dialog.length - 1) {
      setPos(pos + 1);
    }
  }
  // Go back by one dialog
  function back() {
    if (pos > 0) {
      setPos(pos - 1);
    }
  }
  // Save your current position to local storage for later
  function savepos() {
    localStorage.setItem("savepoint", pos.toString());
  }
  // Load the position you saved in local storage and go to it
  function loadpos() {
    setPos(Number(localStorage.getItem("savepoint")));
  }
}
