import React from "react";

export default function Dialogue(props) {
  return (
    <div>
      <button
        onClick={() => {
          props.savepos();
        }}
      >
        Save
      </button>
      <button
        onClick={() => {
          props.loadpos();
        }}
      >
        Load
      </button>
      <button
        onClick={() => {
          props.back();
        }}
      >
        Back
      </button>
    </div>
  );
}
