import React from "react";

export default function CharacterDisplay(props) {
  return (
    <div>
      <svg width="500" height="300">
        {/* Background fallback */}
        <rect width="500" height="300" />
        <image href={props.bg} width="500" />
        <image href={props.portrait} width="250" x="125" y="50" />
      </svg>
    </div>
  );
}
